"use strict";

var Timeline = require("./timeline/Timeline");

/**
 * @description
 * Jibo Expression API
 * @module jibo.express
 * @private
 */
var express = {

	/**
	 * @param {Clock} clock
	 *
	 * @return {Timeline}
	 */
	createTimeline: function(clock)
	{
		return new Timeline(clock);
	}
};

module.exports = express;
